import CardStatusService from '@/services/card/CardStatusService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let CardStatusTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultCardStatusTableFilter: {
      type: String,
      default: null
    },
    defaultCardStatusTableOrderBy: {
      type: String,
      default: null
    },
    statusBehaviourEnumId: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      cardStatusTableData: [],
      cardStatusTableFilterTimer: null,
      cardStatusTableFilter: this.defaultCardStatusTableFilter,
      cardStatusTableOrderBy: this.defaultCardStatusTableOrderBy,
      cardStatusTableContainer: 'loading-container',
      cardStatusTableBusy: false,
      cardStatusTableTotalRows: 0,
      cardStatusTableCurrentPage: 1,
      cardStatusTablePerPage: 10
    }
  },
  computed: {
    cardStatusTableNoDataText: function () {
      return this.cardStatusTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    cardStatusTableTotalPages: function () {
      return Math.ceil(this.cardStatusTableTotalRows / this.cardStatusTablePerPage)
    },
    cardStatusTablePagingInfo: function () {
      return this.pagingInfo(this.cardStatusTableCurrentPage, this.cardStatusTablePerPage, this.cardStatusTableTotalRows)
    }
  },
  methods: {
    loadCardStatusTableData (loadingContainerId) {
      let self = this
      this.cardStatusTableBusy = true
      this.cardStatusTableContainer = loadingContainerId || this.cardStatusTableContainer
      this.startLoading(this.cardStatusTableContainer)
      CardStatusService.cardStatuses({
        pageIndex: this.cardStatusTableCurrentPage - 1,
        pageSize: this.cardStatusTablePerPage,
        filter: this.cardStatusTableFilter,
        orderByField: this.cardStatusTableOrderBy,
        statusBehaviourEnumId: this.statusBehaviourEnumId
      }).then(function (response) {
        self.stopLoading(self.cardStatusTableContainer)
        self.cardStatusTableBusy = false
        self.cardStatusTableTotalRows = response.data.totalRows
        self.cardStatusTableData = response.data.cardStatusList || []
      }).catch(function (error) {
        self.stopLoading(self.cardStatusTableContainer)
        self.cardStatusTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshCardStatusTable () {
      this.loadCardStatusTableData()
    },
    filterCardStatusTable (filter) {
      let self = this
      if (this.cardStatusTableFilter !== filter) {
        clearTimeout(this.cardStatusTableFilterTimer)
        this.cardStatusTableFilterTimer = setTimeout(() => {
          self.cardStatusTableFilter = filter
          self.loadCardStatusTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.cardStatusTableFilterTimer)
      }
    },
    orderCardStatusTable (orderBy, active) {
      this.cardStatusTableOrderBy = (active ? '' : '-') + orderBy
      this.loadCardStatusTableData()
    }
  },
  watch: {
    cardStatusTableCurrentPage: function () {
      this.refreshCardStatusTable()
    },
    cardStatusTablePerPage: function () {
      this.refreshCardStatusTable()
    },
    statusBehaviourEnumId: function () {
      this.refreshCardStatusTable()
    }
  }
}

export default CardStatusTableMixin
